import React, { useState } from 'react';
import BookmarkModal from './BookmarkModal'; //   

const BookmarkButton = ({ shopId, isBookmarked, isLoggedIn, onToggle }) => {
  const [isModalOpen, setIsModalOpen] = useState(false);

  const handleClick = () => {
    if (!isLoggedIn) {
      alert(' .');
      window.location.href = '/member/login';
      return;
    }

    if (isBookmarked) {
      //     ""
      fetch(`/api/bookmarks?shopId=${shopId}`, {
        method: 'DELETE',
        credentials: 'include',
      })
        .then(() => onToggle(false))
        .catch(err => console.error('   :', err));
    } else {
      //    " "
      setIsModalOpen(true);  //   handleClick   
    }
  };

  
  return (
    <>
      <div className="save-button" onClick={handleClick}>
        <svg className="save-icon" width="24" height="24" viewBox="0 0 24 24">
          <polygon
            fill={isBookmarked ? 'black' : 'none'}
            points="20 21 12 13.44 4 21 4 3 20 3 20 21"
            stroke="black"
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth="2"
          />
        </svg>
      </div>

      {/*   */}
      {isModalOpen && (
        <BookmarkModal
          shopId={shopId}
          onClose={() => setIsModalOpen(false)} //  
          onBookmarkSuccess={() => onToggle(true)} //    
        />
      )}
    </>
  );
};

export default BookmarkButton;
