import React, { useEffect, useState } from 'react';
import { fetchMyInfo } from '../service/MypageApi';
import { logout } from '../service/MemberApi';
import './css/Mypage.css'; //   CSS     import
import { useNavigate, Link } from 'react-router-dom';

export default function MyPage() {
  const navigate = useNavigate();
  const [searchQuery, setSearchQuery] = useState('');
  const [info, setInfo] = useState(null);
  const [showPasswordModal, setShowPasswordModal] = useState(false);

  //   
  const handleSearchSubmit = (e) => {
    e.preventDefault();
    if (searchQuery.trim()) {
      navigate(`/search?query=${encodeURIComponent(searchQuery.trim())}`);
    }
  };

  useEffect(() => {
    fetchMyInfo()
      .then(data => {
        console.log("   :", data);
        setInfo(data);
      })
      .catch(err => {
        if (err.response?.status === 401) {
          navigate('/member/login'); //      
        } else {
          console.error(err); //     
        }
      });
  }, [navigate]);


  const handleOpenModal = () => setShowPasswordModal(true);
  const handleCloseModal = () => setShowPasswordModal(false);
  const handleLogout = async () => {
    try {
      await logout();
      navigate('/'); //    
    } catch (error) {
      console.error('  :', error);
    }
  };

  return (
    <div id="root">
      <header className="header-content">
        <div className="common-desk-header">
          <div className="header-wrap">
            <div className="search-form">
              <form onSubmit={handleSearchSubmit}>
                <div className="input-wrap">
                  <input
                    className="search-input"
                    type="search"
                    id="query"
                    name="query"
                    placeholder=",    "
                    maxLength={255}
                    autoComplete="off"
                    value={searchQuery} //   
                    onChange={(e) => setSearchQuery(e.target.value)} //   
                  />
                  <button type="submit" className="btn-search">
                    <img src="https://github.com/user-attachments/assets/19865e59-1076-4b33-ae6a-9cfbd7b5bbb2" alt="" />
                  </button>
                </div>
              </form>
            </div>
            <div className="auth-Box"></div>
          </div>
        </div>
      </header>

      <div className="page-container">
        <div className="profile">
          <div className="name">{info?.name}</div>
          <div className="department">{info?.departmentCollege} / {info?.departmentName}</div>
        </div>

        <div className="content-container profile-info">
          <div className="content-title">
            <span className="content-title-text"> </span>
          </div>
          <div className="content-body">
            <div className="content-item">
              <span className="content-label">:</span>
              <span className="content-value">{info?.name}</span>
            </div>
            <div className="content-item">
              <span className="content-label">/:</span>
              <span className="content-value">{info?.departmentCollege} / {info?.departmentName}</span>
            </div>
            <div className="content-item">
              <span className="content-label">:</span>
              <span className="content-value">{info?.email}</span>
            </div>
            <div className="content-item">
              <span className="content-label">:</span>
              <span className="content-value">********</span>
              <button className="edit-btn" onClick={handleOpenModal}></button>
            </div>
          </div>
        </div>

        <div className="activity-section">
          <h2 className="activity-title">  </h2>
          <div className="activity-grid">
            <div className="activity-card">
              <div className="activity-text">   </div>
              <button className="go-btn" onClick={() => navigate('/myPage/bookmarkList')}></button>
            </div>
            <div className="activity-card">
              <div className="activity-text">   </div>
              <button className="go-btn" onClick={() => navigate('/myPage/likeList')}></button>
            </div>
          </div>
        </div>


        <div style={{ marginTop: '40px', textAlign: 'center' }}>
          <button type="button" className="go-btn" style={{ backgroundColor: '#e74c3c', marginTop: '20px' }} onClick={handleLogout}>
            
          </button>
        </div>

      </div>

      <div className="bottom-nav-wrapper">
        <nav className="bottom-nav">
          <div className="nav-container">
            <Link to="/board/main" className="nav-item">
              <svg className="nav-icon" width="24" height="24" viewBox="0 0 24 24" fill="none">
                <path d="M19 3H5C3.89543 3 3 3.89543 3 5V19C3 20.1046 3.89543 21 5 21H19C20.1046 21 21 20.1046 21 19V5C21 3.89543 20.1046 3 19 3Z" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                <path d="M3 9H21" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                <path d="M9 21V9" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
              </svg>
              <span></span>
            </Link>

            <Link to="/" className="nav-item">
              <svg className="nav-icon" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                <path d="M3 9.5L12 3l9 6.5V21a1 1 0 0 1-1 1h-5a1 1 0 0 1-1-1v-5H10v5a1 1 0 0 1-1 1H4a1 1 0 0 1-1-1V9.5z" />
              </svg>
              <span></span>
            </Link>

            <Link to="/map" className="nav-item">
              <svg className="nav-icon" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                <polygon points="3 6 9 3 15 6 21 3 21 18 15 21 9 18 3 21 3 6" />
                <line x1="9" y1="3" x2="9" y2="18" />
                <line x1="15" y1="6" x2="15" y2="21" />
              </svg>
              <span></span>
            </Link>

            <Link to="/myPage/bookmarkList" className="nav-item">
              <svg class="nav-icon" width="24" height="24" viewBox="0 0 24 24" fill="none"
                stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
                <path d="M3 7h5l2 3h11v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V7z" />
              </svg>
              <span></span>
            </Link>

            <Link to="/myPage" className="nav-item">
              <svg className="nav-icon" width="24" height="24" viewBox="0 0 24 24" fill="none">
                <path d="M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                <circle cx="12" cy="7" r="4" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
              </svg>
              <span></span>
            </Link>
          </div>
        </nav>
      </div>

      {showPasswordModal && (
        <div className="modal" onClick={handleCloseModal}>
          <div className="modal-content content-container" onClick={e => e.stopPropagation()}>
            <div className="content-title">
              <span className="content-title-text"> </span>
              <span className="close" onClick={handleCloseModal}>&times;</span>
            </div>
            <div className="content-body">
              <form action="/member/change-password" method="post">
                <div className="content-item">
                  <label className="content-label"> :</label>
                  <input type="password" name="currentPassword" required />
                </div>
                <div className="content-item">
                  <label className="content-label"> :</label>
                  <input type="password" name="newPassword" required />
                </div>
                <div className="content-item">
                  <label className="content-label">  :</label>
                  <input type="password" name="confirmPassword" required />
                </div>
                <div className="content-item">
                  <button type="submit" className="edit-btn"></button>
                </div>
              </form>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
